* {
    box-sizing: border-box;
}

.error {
    color: red;
}

.body {
    margin: 0;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    transition: 0.5s;
    background: linear-gradient(to left, #FFD53D 50%, #40B0DF 50%);
    background-size: 200% 100%;
    background-position:right bottom;
    transition:all 1.5s ease;
    background-position:left bottom
}


.container {
    width: 800px;
    height: 500px;
    padding: 40px 0;
    position: relative;
}

.block {
    height: 100%;
    background-color: rgba(255, 255, 255, 0.2);
    box-shadow: 0 5px 45px rgba(0, 0, 0, 0.2);
    display: flex;
    align-items: center;
}

.block__item {
    width: 50%;

}

.block-item {
    text-align: center;
}

.block-item__title {
    font-size: 1.2rem;
    font-weight: 500;
    color: #fff;
    margin-bottom: 10px;
}

.block-item__btn {
    border: none;
    cursor: pointer;
    padding: 10px 20px;
    background-color: #fff;
    color: #333;
    font-size: 15px;
    font-weight: 500;
}

.form-box {
    background-color: #fff;
    height: 100%;
    width: 50%;
    box-shadow: 0 5px 45px rgba(0, 0, 0, 0.25);

    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;
    transition: 0.5s ease-in-out; 
    display: flex;
    align-items: center;
    overflow: hidden;
}

.form-box.active .form_signin {
    left: -100%;
    transition-delay: 0s;
}

.form-box.active .form_signup {
    left: 0;
    transition-delay: 0.25s;
}

.form-box.active {
    left: 50%;
}

.body.active {
    background: linear-gradient(to right, #40B0DF 50%, #FFD53D 50%);
    background-size: 200% 100%;
    background-position:left bottom;
    transition:all 1.5s ease;
    background-position:right bottom
}

.form {
    width: 100%;
    padding: 50px;
    font-size: 16px;
    position: absolute;
    transition: 0.5s;
}

.form__title {
    font-size: 1.5rem;
    font-weight: 500;
    color: #333;
}

.form__input {
    width: 100%;
    padding: 10px;
    border: solid 1px #333;
    font-size: inherit;
}

.form__btn {
    text-decoration: none;
    border: none;
    cursor: pointer;
    font-size: inherit;
    background-color: #40B0DF;
    color: #fff;
    padding: 10px 50px;
}

.form__btn_signup {
    background-color: #FFD53D;
}

.form_signin {
    left: 0;
    transition-delay: 0.25s;
}

.form_signup {
    left: 100%;
    transition-delay: 0s;
}

.signup-btn:hover {
    background-color: #FFD53D;
    transition: 1s;
    padding: 11px 22px;
}

.signin-btn:hover {
    background-color: #40B0DF;
    transition: 1s;
    padding: 11px 22px;
}